import React, { useState, useEffect } from 'react';
import axios from 'axios';
import '@fortawesome/fontawesome-free/css/all.css';
import { Link, useNavigate } from 'react-router-dom';
import toastr from 'toastr';
import 'toastr/build/toastr.min.css';
import './register.css'


function Register() {
    const [users, setUsers] = useState([]);
    const [newUser, setNewUser] = useState({ name: '', email: '', phone: '', password: '', status: "false" });
    const [confirmPassword, setConfirmPassword] = useState('');
    const [passwordMatchError, setPasswordMatchError] = useState('');
    const [PhoneError, setPhoneError] = useState('');
    const [UsernameError, setUsernameError] = useState('');
    const [EmailError, setEmailError] = useState('');
    const [passwordError, setPasswordError] = useState('');
    const navigate = useNavigate();
    useEffect(() => {
        const isLoggedIn = localStorage.getItem('isLoggedIn');
        if (isLoggedIn === 'true') {
            navigate('/login');
        }
        toastr.options = {
            closeButton: true,
            progressBar: true,
            positionClass: 'toast-top-right'
        };
        const fetchData = async () => {
            try {
                const response = await axios.get('https://my-json-server.typicode.com/VinhDat04/ApiMovie/users');
                setUsers(response.data);
                console.log("Datannn", response.data);
            } catch (error) {
                console.error('Error fetching data:', error);
            }
        };

        fetchData();
    }, [navigate]);

    const handleChange = (e) => {
        const { name, value } = e.target;
        setNewUser(prevState => ({
            ...prevState,
            [name]: value
        }));
    };

    const handleConfirmPasswordChange = (e) => {
        setConfirmPassword(e.target.value);
    };

    const handleSubmit = async (e) => {
        e.preventDefault();

        const isEmailExist = users.some(user => user.email === newUser.email);
        const isUsernameExist = users.some(user => user.name === newUser.name);

        if (isEmailExist) {
            setEmailError('Email already exists');
            return;
        } else {
            setEmailError('');
        }

        if (isUsernameExist) {
            setUsernameError('Username already exists');
            return;
        } else {
            setUsernameError('');
        }

        if (newUser.name === "") {
            setUsernameError("Please enter a valid Username");
            return;
        } else if (newUser.name.length < 5) {
            setUsernameError("Username must be 5 characters or more");
            return;
        } else if (newUser.name.includes(" ")) {
            setUsernameError("Username cannot contain spaces");
            return;
        } else {
            setUsernameError('');
        }

        const emailPattern = /^[a-zA-Z0-9]+@gmail\.com$/;

        if (!emailPattern.test(newUser.email)) {
            setEmailError('Please enter a valid email (e.g., example@gmail.com)');
            return;
        } else {
            setEmailError('');
        }


        const vietnamesePhoneNumberRegex = /^(0|\\+84)(3|5|7|8|9)(\d{8})$/;
        if (newUser.phone === '') {
            setPhoneError("Please enter a valid Phone number");
            return;
        }
        else if (isNaN(newUser.phone)) {
            setPhoneError("Phone numbers cannot contain numbers");
            return;
        } else
            if (!vietnamesePhoneNumberRegex.test(newUser.phone)) {
                setPhoneError("The phone number is not in the correct format");
                return;
            }
            else {
                setPhoneError('');
            }

        const formPassword = /^(?=.*[A-Z])(?=.*[a-z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{6,}$/;
        if (newUser.password === '') {
            setPasswordError('Please enter a valid Password');
            return;
        }
        else if (!formPassword.test(newUser.password)) {
            setPasswordError('Password must be 6 characters long and have at least 1 uppercase letter, 1 lowercase letter and 1 number');
            return;
        }
        else {
            setPasswordError('');
        }

        if (newUser.password !== confirmPassword || newUser.password === '') {
            setPasswordMatchError('Passwords do not match');
            return;
        }
        else {
            setPasswordMatchError('');
        }

        try {
            const response = await axios.post('https://my-json-server.typicode.com/VinhDat04/ApiMovie/users', newUser);
            setUsers([...users, response.data]);
            setNewUser({ name: '', email: '', phone: '', password: '', status: "false" });
            setConfirmPassword('');
            setPasswordMatchError('');
            toastr.success('Register Successful!');
            // localStorage.setItem('isLoggedIn', 'true');
            navigate('/login');
        } catch (error) {
            console.error('Error adding user:', error);
        }
    };


    return (
        <div className='account_container'>
            <div className='register-form'>
                <h1>Register</h1>
                <form onSubmit={handleSubmit} >
                    <div className='username'>
                        <i className="fa-solid fa-user"></i>
                        <input type='text' placeholder="Username*" name="name" value={newUser.name}
                            onChange={handleChange} />
                    </div>
                    {UsernameError && <small style={{ color: 'red' }}>{UsernameError}</small>}
                    <div className='email'>
                        <i className="fa-solid fa-envelope"></i>
                        <input type='text' placeholder="Email*" name="email" value={newUser.email}
                            onChange={handleChange} />
                    </div>
                    {EmailError && <small style={{ color: 'red' }}>{EmailError}</small>}
                    <div className='phone'>
                        <i className="fa-solid fa-phone"></i>
                        <input type='text' placeholder="Phone*" name="phone" value={newUser.phone}
                            onChange={handleChange} />
                    </div>
                    {PhoneError && <small style={{ color: 'red' }}>{PhoneError}</small>}
                    <div className='password'>
                        <i className="fa-solid fa-lock"></i>
                        <input type='password' placeholder="Password*" name="password" value={newUser.password}
                            onChange={handleChange} />
                    </div>
                    {passwordError && <small style={{ color: 'red' }}>{passwordError}</small>}
                    <div className='re-password'>
                        <i className="fa-solid fa-lock"></i>
                        <input type='password' placeholder="Re-enter Password*" value={confirmPassword}
                            onChange={handleConfirmPasswordChange} />
                    </div>
                    {passwordMatchError && <small style={{ color: 'red' }}>{passwordMatchError}</small>}
                    <button type="submit">Sign up</button>
                    <div className='text'>
                        Already have an account? <Link to="/login">Sign in now!</Link>
                    </div>


                </form>
                {/* <ul>
                {users.map(user => (
                    <li key={user.id}>{user.name} - {user.email}</li>
                ))}
            </ul> */}
            </div>
        </div>

    );
}
export default Register;
